$primary-color: #eaee85;
$secondary-color: #f0f0f0;
$highlight-color: #a9a9a9;
$font-color: rgb(46, 46, 46);
$icon-size: 20px;
$shadow: 0 4px 8px rgba(0, 0, 0, 0.2);

.chat-container {
  position: fixed;
  bottom: 80px;
  right: 50px;
  width: 310px;
  background-color: $primary-color;
  border: 1px solid #ccc;
  border-radius: 15px;
  box-shadow: $shadow;
  padding: 10px;
  color: $font-color;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;

  .input-container {
    display: flex;
    align-items: center;

    .icon-container {
      margin-left: 20px;
    }

    .icon {
      width: $icon-size;
      height: $icon-size;
      fill: #333;
      cursor: pointer;
    }
  }
}

.chat-window {
  position: fixed; // Закрепить в правом нижнем углу
  bottom: 20px;    // Отступ снизу
  right: 20px;     // Отступ справа
  width: 310px;
  height: 400px;
  background-color: $secondary-color;
  border-radius: 15px;
  box-shadow: $shadow;
  display: flex;
  flex-direction: column;
}


  &.open {
    animation: slideUp 0.4s ease forwards;
  }

  &.close {
    animation: slideDown 0.4s ease forwards;
  }

  .chat-header {
    background-color: $primary-color;
    padding: 10px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;

    .icon {
      width: $icon-size;
      height: $icon-size;
      cursor: pointer;
    }
  }

  .chat-body {
    display: flex;
    flex-direction: column; // Сообщения располагаются сверху вниз
    justify-content: flex-end; // Сообщения растут снизу
    padding: 10px;
    background-color: #f8f8f8; // Светло-серый фон
    overflow-y: auto; // Прокрутка, если сообщений слишком много
  }

  .chat-input {
    display: flex;
    flex-direction: column;
    background-color: $highlight-color;
    padding: 10px;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;

    .attached-file-preview {
      display: flex;
      align-items: center;
      background-color: #f5f5f5;
      padding: 5px 10px;
      border-radius: 8px;
      margin-bottom: 5px;

      button.remove-file-button {
        background: none;
        border: none;
        color: #ff0000;
        margin-left: 10px;
        font-size: 14px;
        cursor: pointer;
      }
    }

    .clip-icon {
      width: $icon-size;
      height: $icon-size;
      margin-right: 10px;
      cursor: pointer;
    }

    input {
      flex: 1;
      padding: 5px;
      border-radius: 10px;
      border: none;
    }

    .send-button {
      margin-top: 5px;

      img {
        width: $icon-size;
        height: $icon-size;
      }
    }
  }
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideDown {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(100%);
    opacity: 0;
  }
}

.message-bubble {
  padding: 10px 15px;
  border-radius: 15px;
  margin: 8px 0;
  max-width: 70%;
  font-size: 14px;
  word-wrap: break-word;
  background-color: #ffffff; // Белый фон для отправителя
  color: $font-color;
  border: 1px solid #ccc;
  box-shadow: $shadow;
}

.message-bubble.sent {
  align-self: flex-end; /* Align messages to the right */
  background-color: #a9a9ff; /* Purple color for sent messages */
  color: #fff; /* White text color for contrast */
  margin-right: 10px; /* Add space between the message and the right edge */
  border: 1px solid #8a8aff; /* Slightly darker border for the purple message */
}


.message-bubble.received {
  align-self: flex-start; // Выравнивание сообщений получателя влево
  background-color: #f1f1f1; // Серый фон для полученных сообщений
  margin-left: 10px; // Отступ от левого края
}


.file-attachment {
  color: #007bff;
  font-size: 0.9em;
}
